.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-image: url("https://static.vecteezy.com/system/resources/previews/023/849/538/non_2x/billiard-balls-on-green-table-3d-banner-with-copy-space-vector.jpg");
  background-size: cover;
  background-color: #282c34;
  min-height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.color-filter {
  width: 100%;
  height: 100vh;
}

.flex-container {
  display: flex;
}

.point-screen {
  width: 70%;
  height: 100vh;
}

.fight {
  width: 100%;
  height: 30vh;
  background-color: rgba(10, 10, 30, 0.1);
}

.points {
  width: 100%;
  height: 70vh;
  background-color: rgba(10, 10, 30, 0.1);
}

.team-screen {
  width: 30%;
  height: 100vh;
  background-color: rgba(10, 10, 30, 0.3);
}

.makeTeam-btn {
  padding: 50px;
  margin: 50px;
  border-radius: 20px;
  background-color: #157515;
  color: white;
}

.App-link {
  color: #61dafb;
}

.background {
  width: 100vw;
}
